@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe {
    border-radius: 60px;
    padding: 20px 0;
    margin: 20px;
    width: 400px;
    box-shadow: 8px 5px 5px 2px #9e9c9c26;
    letter-spacing: 1px;
    border: 1px solid #80808024;
  }
  
  .recipe:hover,
  .recipe:active {
    transform: scale(1.02);
    transition: transform 200ms ease-in-out;
  }
  
  .recipe_title {
    text-align: center;
    font-size: 1.6rem;
    color: #565656;
  }
  
  .recipe h2 {
    margin: 0;
    padding: 10px 20px 0px 20px;
    color: #565656;
    font-size: 1.3rem;
  }
  
  .image_div {
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
  }
  
  .image {
    display: block;
    width: 100%;
    margin: -33% 0;
  }
  
  .calories {
    text-align: center;
  }
  
  .ingredients {
    list-style-type: none;
    padding: 10px 20px;
  }
  
  .ingredient {
    padding: 5px 0;
    line-height: 1.4rem;
  }
  
  .ingredient i {
    color: #fb466a;
  }
  
.search-bar {
    max-width: 320px;
    min-width: 220px;
    width: 45%;
    border: none;
    padding: 12px;
    box-shadow: 0 0 5px 0 #00000040;
    border-radius: 20px;
  }
  
  .search-bar::-webkit-input-placeholder {
    color: #00000070;
  }
  
  .search-bar:-ms-input-placeholder {
    color: #00000070;
  }
  
  .search-bar::-ms-input-placeholder {
    color: #00000070;
  }
  
  .search-bar::placeholder {
    color: #00000070;
  }
  
  .search-button {
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
    border: 1px solid #fe9a8b;
    padding: 12px 15px;
    color: white;
    box-shadow: 0 0 5px 0 #00000040;
    border-radius: 20px;
    position: relative;
    right: 43px;
    top: -1px;
  }
  
  .search-bar:focus,
  .search-bar:hover {
    box-shadow: 0 0 5px 2px #00000040;
    outline: none;
  }
  
  .search-button:focus {
    outline: none;
  }
  
* {
  box-sizing: border-box;
}

html {
  border: 15px solid #f9748f;
}

body {
  font-family: "Montserrat", sans-serif;
}

.App {
  min-height: 100vh;
  background: white;
}

.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

.title {
  color: #f9748f;
  font-weight: 200;
  font-size: 3rem;
}

.search-form {
  min-height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}



